import { Box, Button, Typography } from "@mui/material"
import Logo from "../assests/images/hero-img1.png"
const Introduction = () => {
    return (
        <Box style={{ padding: 70 , display:"flex" , alignItems:"center"}}>
            <Box style={{ width: "20%" }}>
                <Typography style={{ fontSize: 30, fontWeight: 600 }}>Anytime AnyWhere Learn on your Suitable Schedule</Typography>
                <Typography style={{ fontSize: 16, wordWrap: "break-word", marginTop: 10, opacity: 0.5 }}>lorem ipsum husdxwduygsudysdghysysdhwebhxsbjhjwgdwduwedgwdjwedwedwhjbcdscsjhbds</Typography>
                <Box style={{ position: "relative" }} >
                    <input placeholder="Search" style={{ marginTop: 10, height: 40, boxSizing: "border-box", borderRadius: "20px", fontSize: 16, padding: 10, width: "100%" , borderRight: "4px solid transparent " }} />
                    <Button variant="contained" style={{ borderRadius: 20, boxSizing: "border-box", position: "absolute", right: -30 , top:12 }}>Search</Button>
                </Box>
            </Box>
            <Box style={{marginLeft:"auto" ,  marginRight : 20}}>
<img src={Logo} style={{width:500}} />
 
            </Box>
        </Box>
    )
}

export default Introduction; 