import { Box, Typography, styled } from "@mui/material"
import PhoneIcon from "@mui/icons-material/LocalPhone";
import Logo from "@mui/icons-material/NotificationsNone" ;
import {Link} from "react-router-dom" ; 
const Header = () => {

    const Container = styled(Box)`
     margin-top : 20px ; 
     display : flex ; 
      align-items : center
     
     `

    const Text = styled(Typography)`
    margin-left : 50px ;
    font-size: 19px; 
    font-family : roboto ; 
    opacity : 1;
    cursor : pointer ; 
    transition : 0.5s
    `


    return (
        <Container>
            <Box>
                
                <Text style={{ fontSize: 28 }}><i>Knowledge School</i></Text>

            </Box>
            <Box style={{ marginLeft: "auto", marginRight: 20, display: "flex", alignItems: "center" }}>
            <Link to="/" style={{textDecoration:"none" , color : "#000"}} > < Text className = "Header">Home</ Text></Link>
               <Link to="/About_Us" style={{textDecoration:"none" , color : "#000"}} ><Text className = "Header">About us</Text></Link>
               <Link to="/courses" style={{textDecoration:"none" , color : "#000"}} > <Text className = "Header">Courses</Text></Link>
               <Link to="/Pages" style={{textDecoration:"none" , color : "#000"}} >  <Text className = "Header">Page</Text></Link>
               <Link to="/Blogs" style={{textDecoration:"none" , color : "#000"}} ><Text className = "Header">Blogs</Text></Link>
                <PhoneIcon style={{ fontSize: 19, marginRight: 2, marginLeft: 20, color: "#17bf9e" }} /> 
                <Text  className = "Header" style={{  marginLeft: 3, marginRight: 100 }}>9729878900</Text> 
            </Box>
        </Container>
    )
}

export default Header; 