import { Box, Typography } from "@mui/material"
import Aboutus from "../assests/images/about-us.png"
const About = () => {
    return (
        <Box style={{width:"100%"}}>
            <Box style={{ display: "flex", padding: 70, boxSizing: "border-box" , width:"100%" }}>
                <Box>
                    <img src={Aboutus} width = {"100%"}/>
                </Box>
                <Box style={{ width:"80%" , marginLeft : 20}}>
                    <Typography variant="h5">About Us</Typography>
                    <Typography style={{wordBreak:"break-word" , opacity:0.5}}>Lorem ipsum dolor sit amet consectetur adipisicing elit. Veniam, magni, numquam deleniti illum sequi aliquam quasi, maiores esse molestias nobis natus commodi vel adipisci quidem quis reprehenderit itaque architecto saepe?</Typography>
                </Box>
            </Box>
        </Box>
    )
}
export default About; 